import Canvas2D from "../Components/Canvas2D";
import Form from "../Form";
import '../App.css';
import PointForm from "../Components/PointForm";
import { useState, useEffect, useRef } from "react";


function generateRandomInt(max) {
  return Math.floor(Math.random() * Math.floor(max));
}

function Interactive(props) {
  const container = useRef(null);
  const [points, setPoints] = useState([]);
  const [angle, setAngle] = useState(0);
  const encryptedKey = useRef(null);
  const userId = useRef(null);
  const currentPoints = useRef(points);
  const updatePoints = useRef(points);

  function updatePoint(i, x, y) {
    currentPoints.current[i] = { x: x, y: y };
    updatePoints.current[i](x, y);
    setPoints(currentPoints.current.map((point) => point));
    setAngle(calculateAngle(currentPoints.current[0], currentPoints.current[1]));
  }

  function renderForm() {
    return points.map((point, i) => {
      return (
        <PointForm
          name={i}
          updatePoint={(x, y) => updatePoint(i, x, y)}
          point={{ ...point }}
        />
      );
    });
  }

  function onSubmit(values, form) {
    const ans = parseInt(values['x']) + ',' + parseInt(values['y']);
    const key = parseInt(points[1]['x'] - points[0]['x']) + ',' + parseInt(points[1]['y'] - points[0]['y']);
    if (ans === key) {
      alert("Correct");
    } else {
      alert("Incorrect");
    }
    console.log(ans, key);
    /*
      Temporary mock verification

      Need to use Question component 
    */
    /*
    const ans = values["x"] + "," + values["y"];
    const body = {
      key: encryptedKey.current,
      id: userId.current,
      ans: ans,
    };
    
    const url = new URL("http://localhost:3322/tasks/content/interactive/verify");
    fetch(url, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify(body),
    })
      .then((response) => response.json())
      .then((data) => {
        if(data["success"]) {
          alert("Answer is correct");
        } else {
          alert("Answer is incorrect");
        }
      });
    */
  }

  function addToCanvas(canvas, data) {
    const newLines = data["vectors"];
    let toPoints = [...currentPoints.current];

    for (let i = 0; i < newLines.length; ++i) {
      var objs = canvas.addLine(newLines[i][0], newLines[i][1]);
      let length = toPoints.length;
      for (let i = 0; i < objs["points"].length; ++i) {
        const point = objs["points"][i];
        point.onDragEnd = (position) =>
          updatePoint(length + i, position["x"], position["y"]);
      }

      toPoints = [...toPoints, ...objs["points"]];
    }

    currentPoints.current = toPoints.map((point) => point.position);
    updatePoints.current = toPoints.map((point) => point.updatePosition);
    encryptedKey.current = data['key'];
    userId.current = data['id'];
    setPoints(currentPoints.current.map((point) => point));
    setAngle(calculateAngle(currentPoints.current[0], currentPoints.current[1]));
  }

  useEffect(() => {
    const canvas = new Canvas2D(container);
    /*
    const url = new URL("http://localhost:3322/tasks/content/interactive");

    let formValues = {
      type: "vector",
    };

    fetch(url, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify(formValues),
    })
      .then((response) => response.json())
      .then((data) => {
        addToCanvas(canvas, data);
      });
    */

    //Temporary mock data not from server

    const newLines = [
      [[generateRandomInt(30), generateRandomInt(30)], [generateRandomInt(30), generateRandomInt(30)]]
    ];
    addToCanvas(canvas, { vectors: newLines });
  }, []);


  function calculateAngle(fPoint, sPoint) {
    console.log(fPoint);
    console.log(sPoint);
    var x1 = fPoint['x'];
    var y1 = fPoint['y'];
    var x2 = sPoint['x'];
    var y2 = sPoint['y'];
    var deltaY = y2 - y1;
    var deltaX = x2 - x1;
    return Math.round((Math.atan2(deltaY, deltaX)) * 180 / Math.PI);
  }


  return (
    <>
      <div id="Demo">
        <canvas id="DemoCanvas" ref={container}></canvas>
      </div>
      <Form onSubmit={onSubmit}>
        <div id="material">
          <h1> Magnitude of a Vector </h1>
          <p> Calculate Fx and Fy given the provided data and formulas below: </p>
          <p> Round your answers to the nearest whole number! </p>
          <table className="center">
            <tr id="tableheader">
              <th>Operation</th>
              <th>Input/Result</th>
            </tr>
            {renderForm()}
            <tr>
              <td>Angle</td>
              <td> <input name="angle" value={angle} placeholder={angle} type="text"></input></td>
            </tr>
            <tr id="rowFX">
              <td>Fx</td>
              <td><input name="x" id="Fx" type="text" placeholder="Fx" ></input></td>
            </tr>
            <tr id="rowFY">
              <td>Fy</td>
              <td><input name="y" id="Fy" type="text" placeholder="Fy"></input></td>
            </tr>
          </table>
          <button type="submit">Submit</button>
          <p id="output"></p>
          {/* <p> Calculate Fx and Fy given the formulas below: </p> */}
          <p> The force (F) is the √(Fx² + Fy²) </p>
          <p> Fx = F*cos(θ) </p>
          <p> Fy = F*sin(θ) </p>
        </div>
      </Form>
    </>
  );
}

export default Interactive;
