import * as THREE from "three";

class VectorArrow extends THREE.Object3D {

	constructor(start, end, lineMaterial) {

		super();
		// dir is assumed to be normalized

        this.type = 'VectorArrow';
        this.lines = [];
        for(let i = 0; i < 3; ++i) {
            const geometry = new THREE.BufferGeometry().setFromPoints([new THREE.Vector3(0, 0, 0), new THREE.Vector3(0, 1, 0)]);
            const line = new THREE.Line(geometry, lineMaterial);
            this.lines.push(line);
            this.add(line);
        }

        this.start = start;
        this.end = end;

        this.update();

    }
    
    set(start, end) {
        this.start = start;
        this.end = end;
        this.update();
    }

    update() {
        const direction = this.end.clone().sub(this.start);
        const length = direction.length();

        let position = this.lines[0].geometry.getAttribute("position");
        position.set([0, 0, 0, 0, 0, length], 0);
        position.needsUpdate = true;

        position = this.lines[1].geometry.getAttribute("position");
        position.set([0, 0, length, 0, 0.4, length - 0.5], 0);
        position.needsUpdate = true;

        
        position = this.lines[2].geometry.getAttribute("position");
        position.set([0, 0, length, 0, -0.4, length - 0.5], 0);
        position.needsUpdate = true;

        this.position.copy(this.start)

        this.lookAt(this.end);
    }

    copy(source) {
        super.copy(source, false);
        this.start = source.start;
        this.end = source.end();
        return this;
    }

}


export { VectorArrow };
