import React from "react";
import { Link, withRouter } from "react-router-dom";

function Navigation(props) {
  return (
    <div className="navigation">
      <nav class="navbar navbar-expand navbar-light bg-white">
        <div class="container">
          <Link font-weight="bolder" class="navbar-brand" to="/Page2Demo">
            Vectors
          </Link>

          <div>
            <ul class="navbar-nav ml-auto">
              <li class={`nav-item  ${props.location.pathname === "/" ? "active" : ""}`}>
                <Link class="nav-link" to="/">
                  Lesson
                  <span class="sr-only">(current)</span>
                </Link>
              </li>
              <li class={`nav-item  ${props.location.pathname === "/Page2Demo" ? "active" : ""}`}>
                <Link class="nav-link" to="/Page2Demo">
                  Demo
                </Link>
              </li>
              <li class={`nav-item  ${props.location.pathname === "/Page3Quiz" ? "active" : ""}`}>
                <Link class="nav-link" to="/Page3Quiz">
                  Quiz
                </Link>
              </li>
            </ul>
          </div>
        </div>
      </nav>
    </div>
  );
}

export default withRouter(Navigation);