import * as THREE from "three";
import { DragControls } from "three/examples/jsm/controls/DragControls";
import {VectorArrow} from "./VectorArrow";

class Canvas2D {
  constructor(canvasRef) {
    this.scene = new THREE.Scene();
    this.camera = new THREE.OrthographicCamera(
      0,
      0,
      0,
      0,
      1,
      10000
    );
    
    this.camera.position.set(10, 10, 100);
    this.camera.lookAt(10, 10, 0);

    this.renderer = new THREE.WebGLRenderer({ antialias: true, canvas: canvasRef.current});
    this.renderer.setPixelRatio(window.devicePixelRatio);

    this._resizeIfChange();

    this._lineMaterial = new THREE.LineBasicMaterial({
      color: 0x0000ff,
      linewidth: 0.3,
      opacity: 0.35,
    });
    this._pointMaterial = new THREE.MeshBasicMaterial({ color: 0xff0000 });
    this.gridHelper = new THREE.GridHelper(100, 100, 0x888888, 0xCCCCCC);
    this.gridHelper.rotateX(Math.PI / 2);
    this.gridHelper.position.setZ(-0.5);
    this.scene.background = new THREE.Color(0xFFFFFF);
    this.scene.add(this.gridHelper);

    window.addEventListener("resize", () => { this._resizeIfChange(); 
      this._render(); });

    this._render();
  }

  _updateLine(dLine) {
    return () => {
        const points = dLine.points;
        dLine.set(points[0], points[1]);

        this._render();
    }
  }

  _render() {
    this.renderer.render(this.scene, this.camera);
  }
  
  _addPoints(points) {
    const dPoints = [];
    for (let i = 0; i < points.length; ++i) {
      const point = points[i];
      const pointGeom = new THREE.CircleGeometry(0.1, 32);
      const dPoint = new THREE.Mesh(pointGeom, this._pointMaterial);

      dPoint.position.copy(point);
      dPoints.push(dPoint);
      points[i] = dPoint.position;

      this.scene.add(dPoint);
    }
    return dPoints;
  }

  _createUpdatePositionFunction(point, updateLine) {
    point.updatePosition = (x, y) => {
      point.position.x = x;
      point.position.y = y;
      point.needsUpdate = true;
      updateLine();
      this._render();
    }
  }

  _resizeIfChange() {
    const canvas = this.renderer.domElement;
    const width = canvas.clientWidth;
    const height = canvas.clientHeight;

    if(canvas.width !== width || canvas.height !== height) {
      this._resize(width, height);
    }
  }
  
  _resize(width, height) {
    const widthAspect = Math.max(1, width / height);
    const heightAspect = Math.max(1, height / width);

    this.renderer.setSize(width, height, false);
    console.log(width);
    console.log(height);

    this.camera.left = -20 * widthAspect;
    this.camera.right = 20 * widthAspect;
    this.camera.top = 20 * heightAspect;
    this.camera.bottom = -20 * heightAspect;
    
    this.camera.updateProjectionMatrix();
  }

  _onDragEnd(event) {
    const pos = event.object.position;
    const x = Math.round(pos['x']);
    const y = Math.round(pos['y']);
    event.object.updatePosition(x, y);
    if(event.object.onDragEnd) {
      event.object.onDragEnd(event.object.position);
    }
  }

  addLine(fPoint, sPoint) {
    const points = [];
    points.push(new THREE.Vector3(fPoint[0], fPoint[1], 0));
    points.push(new THREE.Vector3(sPoint[0], sPoint[1], 0));

    const dPoints = this._addPoints(points);

    const controls = new DragControls(
      dPoints,
      this.camera,
      this.renderer.domElement
    );

    const dLine = new VectorArrow(points[0], points[1], this._lineMaterial);
    dLine.points = points;

    const updateLine = this._updateLine(dLine);
    controls.addEventListener("drag", updateLine);
    controls.addEventListener("dragend", this._onDragEnd);

    for(let i = 0; i < dPoints.length; ++i) {
      this._createUpdatePositionFunction(dPoints[i], updateLine);
    }

    this.scene.add(dLine);

    this._render();

    return {
      points: dPoints,
      line: dLine,
    }
  }
}

export default Canvas2D;