import { useState, useEffect, useRef } from "react";

function PointForm(props) {
  return (
    <>
      <tr>
        <td>{"X" + props.name}</td>
        <td>
          <input
            onChange={(event) => {
              props.updatePoint(event.target.value, props.point["y"]);
            }}
            name={"x" + props.name}
            type="number"
            value={props.point["x"]}
          />
        </td>
      </tr>
      <tr>
        <td>{"Y" + props.name}</td>
        <td>
          <input
            onChange={(event) => {
              props.updatePoint(props.point["x"], event.target.value);
            }}
            name={"y" + props.name}
            type="number"
            value={props.point["y"]}
          />
        </td>
      </tr>
    </>
  );
}

export default PointForm;
